.quiz, .result {
  max-width: 750px;
  margin: 30px auto;
}

.answerOptions {
  padding: 0;
  list-style-type: none;
}

.radioCustomButton {
  float: left;
}

.radioCustomLabel {
  display: block;
  margin-left: 28px;
  padding-left: 6px;
  margin-top: 25px;
  font-size: 1.2em;
  line-height: 1.5em;
}


.radioCustomLabel:hover {
  background-color: #e5e5e5;
}

.result {
  text-align: center;
  font-size: 2em;
  font-weight: bold;
  text-transform: uppercase;
}

.resultText {
  margin-top: -45px;
}

.kvasyk {
  width: 50%;
}